html,
body {
  	background: url("../assets/background.jpeg");
    background-size: cover;
}
header{
	background: none;
}




/*
  PAGES
  */
  .pages{
  	margin-top: 50px;
  	padding-top: 140px;
  }

  .single-page .page-icon {
  	font-size: 3em;
  	margin-bottom: 0px;
  }

  .single-page h3{
  	font-weight: bold;
  	font-size: 30px;
  	margin-top: 0px;
  	margin-bottom: 5px;

  	display: inline-block;
  	vertical-align: middle;
  	line-height: normal;
  }


  .single-page{
  	cursor: pointer;
  	padding: 5px 5px 5px 5px;
  	margin: 0 0 30px 0;
  	border-left: 4px solid #55AA00;
  	border-right: 4px solid #55AA00;
  	border-radius: 35px;
  	-webkit-transition: all ease 0.55s;
  	transition: all ease 0.55s;
  	line-height: 150px;
  	text-align: center;
  }


  .single-page:hover{
  	background-color: #55AA00;
  }

  .account .btn{
	width:100%;
  }

